import React, { useEffect, useState } from "react";
import axios from "axios";
import Cookies from 'js-cookie';
import { connect } from 'react-redux';
import { useNavigate } from "react-router-dom";
import {Link} from 'react-router-dom'
import { cart } from '../redux/action/cart'

const Cart = ({name_global}) => {
  const [isLoading, setLoading] = useState(true);
  const [handleErr, setErr] = useState(null);
  const [data, setData] = useState(null)
  const navigate = useNavigate();
  console.log(name_global)
  useEffect(() => {
    const config = {
      headers: {
        "Accept": "application/json",
        "Content-Type": "application/json",
        'X-CSRFToken': Cookies.get('csrftoken')
      }
    };

    try {
      axios.get("http://localhost:8000/api-cart/", config)
        .then(response => {
          setData(response.data)
        })
        .catch(err => {
          // Handle API request error
          setErr(err.message);
        })
        .finally(() => {
          setLoading(false);
        });
    } catch (error) {
      // Handle other errors
      setErr(error.message);
      navigate('/');
    }
  }, [navigate]);

  return (
    <div>
      {handleErr && <p>Error: {handleErr}</p>}
      {isLoading && <h1>Loading...</h1>}
      <h2>Your Cart</h2>
      { data && data.cart_items.length > 0 ? (
        <ul>
            <main className="mt-5 pt-5" style={{marginbottom: "135px"}}>
              <div className="container">
                <h2 className="mb-3 pb-2 mt-3">Your Cart</h2>
                  <div className="table-responsive">
                      <table className="table">
                            <thead>
                              <tr>
                                <th scope="col">pic</th>
                                <th scope="col">Name</th>
                                <th scope="col">quantity</th>
                                <th scope="col">price</th>
                                <th scope="col">total</th>
                              </tr>
                            </thead>
                            {data.cart_items.map((item) => (
                                

                                  <tr key={item.id}>

                                      <td>
                                        <Link className="col-lg-10" to={`/${item.id}`}>
                                          <img style={{height: "140px" , width: "99px" }} src={item.pic} className="card-img-top" alt=""/>
                                        </Link>
                                      </td>

                                      <td>
                                        <Link className="col-lg-10" to={`/${item.id}`}>
                                          { item.name }
                                        </Link>
                                      </td>

                                      <td>
                                        <a href="{% url 'store:remove_one_item' order_item.item.pk %}"><i className="fas fa-minus-circle"></i></a>
                                        { item.quantity }
                                        <a href="{% url 'store:add_to_cart' order_item.item.pk %}"><i className="fas fa-plus-circle"></i></a>
                                      </td>


                                      
                                        <td>
                                          <td>${ item.price }</td>
                                          {/* <td>${ item.get_total_discount_price }
                                            <Link style="color:#2196f3" className="col-lg-10" to={`/${item.id}`}>
                                              <i class="fas fa-trash float-right"></i>
                                            </Link>
                                          </td> */}
                                        </td>
                                        <td>
                                          {item.total}
                                        </td>
                                        {/* <td>
                                          <td>${ item.item.price }</td>
                                          <td>${ item.get_total_price }
                                        
                                            <Link style="color:#2196f3" className="col-lg-10" to={`/${item.id}`}>
                                                <i class="fas fa-trash"></i>
                                            </Link>
                                          </td>
                                        </td> */}
                                      
                                  </tr>





                                    
                                
                              
                            ))}
                          <tr>
                            <td colspan="4"><b>Total price</b></td>
                            <td>
                              <b>${ data.cart_total }</b>
                            </td>
                          </tr>
                          <tr >
                            <td colspan="5">
                              <div className="text-center">
                                <a className="btn btn-warning mx-2 float-right" style={{ padding: "13px" }} href="#">Checkout</a>
                                <Link className="btn btn-primary mx-2 float-right" to="/">Continue shopping</Link>
                              </div>
                            </td>
                          </tr>
                      </table>
            
                  </div>

              </div>
            </main>
        </ul>
      ) : (
        <p>Your cart is empty.</p>
      )}
    </div>
  );
};

// const mapStateToProps = (state) => ({
//   cartItems: state.cart.items,
// });

export default Cart;
