// cartReducer.js

import { ADD_TO_CART_SUCCESS, ADD_TO_CART_FAIL, REMOVE_FROM_CART_SUCCESS, REMOVE_FROM_CART_FAIL, ADD_TO_WISH_SUCCESS, ADD_TO_WISH_FAIL } from '../action/type';

const initialState = {
  cartItems: [], // You can adjust the structure of cartItems as needed
  error: null,
  wish: false
};

const cartReducer = (state = initialState, action) => {
  const {type, payload} = action
  switch(type) {
    case ADD_TO_CART_SUCCESS:
      return {
        ...state,
        error: null,
      };
    case ADD_TO_CART_FAIL:
    case REMOVE_FROM_CART_FAIL:
      return {
        ...state,
        error: 'Cart operation failed',
      };
    case REMOVE_FROM_CART_SUCCESS:
      return {
        ...state,
        cartItems: state.cartItems.filter((item) => item.id !== action.payload),
        error: null,
      };
    case ADD_TO_WISH_SUCCESS:
      return {
        ...state,
        wish: payload.wish
      };
    case ADD_TO_WISH_FAIL:
      return {
        ...state,
        error: 'wish failed'
      };
    default:
      return state;
  }
};

export default cartReducer;