// cartActions.js
import Cookies from 'js-cookie'
import axios from 'axios';
import { ADD_TO_CART_SUCCESS, ADD_TO_CART_FAIL, REMOVE_FROM_CART_SUCCESS, REMOVE_FROM_CART_FAIL, ADD_TO_WISH_SUCCESS, ADD_TO_WISH_FAIL } from './type';

export const addToCart = (itemId) => async (dispatch) => {
    const config = {
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'X-CSRFToken': Cookies.get('csrftoken'), // Include the CSRF token
        },
      };
      
  try {
    const res = await axios.post(`${process.env.REACT_APP_API_URL}/${itemId}/add_to_cart/`, {}, config);
    if (res.data.success) {
      dispatch({
        type: ADD_TO_CART_SUCCESS,
      });
    } else {
      dispatch({
        type: ADD_TO_CART_FAIL,
      });
    }
  } catch (error) {
    console.error('Error adding to cart:', error);
    dispatch({
      type: ADD_TO_CART_FAIL,
    });
  }
};

export const removeFromCart = (itemId) => async (dispatch) => {
    const config = {
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'X-CSRFToken': Cookies.get('csrftoken'), // Include the CSRF token
        },
      };
      
  try {
    const res = await axios.post(`${process.env.REACT_APP_API_URL}/${itemId}/rm_from_cart/`, {}, config);
    if (res.data.success) {
      dispatch({
        type: REMOVE_FROM_CART_SUCCESS,
        payload: itemId,
      });
    } else {
      dispatch({
        type: REMOVE_FROM_CART_FAIL,
      });
    }
  } catch (error) {
    console.error('Error removing from cart:', error);
    dispatch({
      type: REMOVE_FROM_CART_FAIL,
    });
  }
};




export const wish = (itemId) => async (dispatch) => {
  const config = {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'X-CSRFToken': Cookies.get('csrftoken'), // Include the CSRF token
      },
    };
    
try {
  const res = await axios.post(`${process.env.REACT_APP_API_URL}/${itemId}/api-wish/`, {}, config);
  if (res.data.success) {
    dispatch({
      type: ADD_TO_WISH_SUCCESS,
      payload: res.data.wish
    });
  } else {
    dispatch({
      type: ADD_TO_WISH_FAIL,
    });
  }
} catch (error) {
  console.error('Error adding to cart:', error);
  dispatch({
    type: ADD_TO_WISH_FAIL,
  });
}
};
