import React from 'react';
import { useParams } from 'react-router-dom';
import CSRFToken from '../component/CSRFToken';
import { connect } from 'react-redux'; // Import connect
import { addToCart } from '../redux/action/cart.js'; // Import the addToCart action

const AddItemCart = ({ addToCart, isAuthenticated }) => {
  const { itemId } = useParams();
  console.log(isAuthenticated)

  const handleSubmit = () => {
    addToCart(itemId);
  };

  return (
    <div>
      <form>
        <CSRFToken />
        <button onClick={handleSubmit} className="btn btn-danger">
          Add to Cart
        </button>
      </form>
    </div>
  );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
  })
  

export default connect(mapStateToProps, { addToCart })(AddItemCart); // Connect and map the addToCart action
