import React from 'react';
import { Link, useParams } from 'react-router-dom';
import CSRFToken from '../component/CSRFToken';
import { connect } from 'react-redux'; // Import connect
import { wish } from '../redux/action/cart.js'; // Import the addToCart action

const Wish = ({ wish, isAuthenticated, wished, user_g }) => {
  const { itemId } = useParams();
  console.log(isAuthenticated)

  const handleSubmit = () => {
    wish(itemId);
  };
  console.log(user_g)
  console.log(wished)
  return (
    <div>
      <form>
        <CSRFToken />
        <button onClick={handleSubmit} className="btn btn-danger">
            {user_g in wished?
                "wished"
                :
                "wish"
            }
        </button>
        
      </form>
    </div>
  );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    user_g: state.profile.user
  })
  

export default connect(mapStateToProps, { wish })(Wish); // Connect and map the addToCart action