import { useState, useEffect } from "react"
import {Link} from 'react-router-dom'
import axios from "axios"
import Cookies from 'js-cookie'


const Home = ({q}) => {
    const [data, setData] = useState(null)
    const [isLoading, setLoading] = useState(true)
    const [handleErr, setErr] = useState(null)
    const url = q? `http://localhost:8000/api-search/?q=${q}` : `http://localhost:8000/api-item/`

    useEffect(() => {
        const config = {
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "X-CSRFToken": Cookies.get('csrftoken')
            }
        }
        axios.get(url, config)
        .then((res) => {
            setData(res.data)
            setLoading(false);
            setErr(null);
        })
        .catch(err =>{
            setLoading(false);
            setErr(err.message);
        })
    }, [q]);

    return (
        <div>
            {handleErr && {handleErr}}
            {isLoading && <h1>Loading...</h1>}
            { data &&  <div  className=''>
                {data.map((item) => (
                    <div key={item.id} className='row mt-3'>
                            <div className="col-lg-1"></div>
                                <div className="col-lg-3 col-md-6 mb-4">
                                    <Link to={`/${item.id}`}>
                                        <div className="card">
                                            <div className="view overlay">
                                                <img src={item.image} class="card-img-top"
                                                    alt=""/>
                                                <a href="{% url 'store:detail' instance.pk %}">
                                                    <div className="mask rgba-white-slight"></div>
                                                </a>
                                            </div>

                                            <div className="card-body text-center">
                                                <h5>{ item.category }</h5>
                                            <h5>
                                                <strong>
                                                <h3>{item.title}</h3>
                                                {item.label=== "SALE" ?
                                                <span className="badge badge-pill warning-color">{ item.label }</span> :
                                                <span className="badge badge-pill danger-color">{ item.label }</span>
                                                }
                                                </strong>
                                                
                                            </h5>

                                            <h4 class="font-weight-bold blue-text">
                                                <strong>
                                                    {item.discount_price?
                                                    <span className="badge badge-pill warning-color">{ item.discount_price }</span> :
                                                    <span className="badge badge-pill danger-color">{ item.price }</span>
                                                    }
                                                </strong>
                                            </h4>
                                            </div>
                                        </div>
                                    </Link>
                                </div>
                    </div>
                ))}
            </div>}
        </div>
    )
}

export default Home