import axios from 'axios';
import {
  PROFILE_SUCCESS,
  PROFILE_FAIL,
  PROFILE_UPDATE_SUCCESS,
  PROFILE_UPDATE_FAIL,
  USER_UPDATE_SUCCESS,
  USER_UPDATE_FAIL,
} from './type';
import Cookies from 'js-cookie';

const getHeaders = () => {
  return {
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'X-CSRFToken': Cookies.get('csrftoken'),
    },
  };
};

export const profile = () => async (dispatch) => {
  try {
    const res = await axios.get(`${process.env.REACT_APP_API_URL}/api-profile/`, getHeaders());

    if (res.data.error) {
      dispatch({
        type: PROFILE_FAIL,
        payload: res.data.error, // Include an error message
      });
    } else {
      dispatch({
        type: PROFILE_SUCCESS,
        payload: res.data,
      });
    }
  } catch (err) {
    dispatch({
      type: PROFILE_FAIL,
      payload: err.message, // Include an error message
    });
  }
};

export const profile_update = (form_data) => async (dispatch) => {
  try {
    const res = await axios.put(`${process.env.REACT_APP_API_URL}/api-profile-update/`, form_data, getHeaders());

    if (res.data.success) {
      dispatch({
        type: PROFILE_UPDATE_SUCCESS,
        payload: res.data,
      });
    } else {
      dispatch({
        type: PROFILE_UPDATE_FAIL,
        payload: res.data.error, // Include an error message
      });
    }
  } catch (err) {
    dispatch({
      type: PROFILE_UPDATE_FAIL,
      payload: err.message, // Include an error message
    });
  }
};

export const user_update = (username) => async (dispatch) => {
  const body = JSON.stringify({ username });

  try {
    const res = await axios.put(`${process.env.REACT_APP_API_URL}/api-profile-user-update/`, body, getHeaders());

    if (res.data.success) {
      dispatch({
        type: USER_UPDATE_SUCCESS,
        payload: res.data,
      });
    } else {
      dispatch({
        type: USER_UPDATE_FAIL,
        payload: res.data.error, // Include an error message
      });
    }
  } catch (err) {
    dispatch({
      type: USER_UPDATE_FAIL,
      payload: err.message, // Include an error message
    });
  }
};