import { useFetch } from "../useFetch"
import { useParams, Link } from "react-router-dom"
import {connect} from 'react-redux'
import CSRFToken from '../component/CSRFToken'
import { useState } from "react"
import 'bootstrap/dist/css/bootstrap.min.css';
import {removeFromCart, addToCart} from "../redux/action/cart"
import AddItemCart from "./AddToCart"
import RmItem from "./RmFromCart"
import Wish from "./wish"

const Detail = ({removeFromCart, addToCart, isAuthenticated}) => {
    const { itemId } = useParams();
    console.log(isAuthenticated)
  
    const handleSubmit = () => {
      addToCart(itemId);
    };
    const {data:item, isLoading, handleError} = useFetch(`http://localhost:8000/api-item/${itemId}/`)

    if (isLoading) {
        return <h1>Loading...</h1>;
    }
    if (!item) {
        return <div>No item data available</div>;
    }
    let category_ = null;
    if (item.category === 'shirt') {
        category_ = (
          <a href="/store/shirt">
            <span className="badge purple mr-1">{item.category}</span>
          </a>
        );
      } else if (item.category === 'pants') {
        category_ = (
          <a href="/store/pants">
            <span className="badge blue mr-1">{item.category}</span>
          </a>
        );
      } else {
        // Add a default option here
        category_ = (
          <a href="/store/default">
            <span className="badge gray mr-1">{item.category}</span>
          </a>
        );
      }
    return (
        <div>
            {handleError && {handleError}}
            {isLoading && <h1>Loading...</h1>}
            {item && 

                <main className="mt-5 pt-4">
                    <div className="container dark-grey-text mt-5">

                        <div className="row wow fadeIn">

                            <div className="col-md-6 mb-4">
                                <a href={ item.image.url }>
                                    <img src={ item.image } className="img-fluid" alt=""/>
                                </a>
                            </div>

                            <div className="col-md-6 mb-4">

                                <div className="p-4">

                                    <div className="mb-3">
                                        {category_}
                                    </div>
                                    {item.discount_price? 
                                        <p className="lead">
                                            <span className="mr-1">
                                            <del>${ item.price }</del>
                                            </span>
                                            <span>${ item.discount_price }</span>
                                        </p>
                                        :
                                        <p className="lead">
                                            <span>${ item.price }</span>
                                        </p>
                                    }
                                    <p className="lead font-weight-bold">Description</p>

                                    <p className="mb-5">{ item.description }</p>

                                    <div>
                                        <AddItemCart />
                                        <RmItem />
                                        <Wish wished={item.wish}/>
                                    </div>

                                    
                                    {/* { request.user.is_authenticated ?
                                        {  ?
                                            <button id='save-button' class="btn btn-info btn-md my-0 p m-1">
                                                wished
                                                <i class="fas fa-shopping-cart ml-1"></i>
                                            </button>
                                        :
                                            <button id='save-button' class="btn btn-info btn-md my-0 p m-1">
                                                wish
                                                <i class="fas fa-shopping-cart ml-1"></i>
                                            </button>
                                        }
                                        :<></>
                                    } */}
                                </div>

                            </div>

                        </div>

                        <hr/>

                        <div className="row d-flex justify-content-center wow fadeIn">

                            <div className="col-md-6 text-center mb-5">

                                <h4 className="my-4 h4">Additional information</h4>

                                <p>{ item.info }</p>

                            </div>

                        </div>

                        <div className="row wow fadeIn" style={{marginbottom: "100px"}}>

                            <div className="col-lg-4 col-md-12 mb-5">
                                <a href="{{ item.image_2.url }}">
                                    <img src="{{ item.image_2.url }}" className="img-fluid" alt=""/>
                                </a>
                            </div>

                            <div className="col-lg-4 col-md-6 mb-5">
                                <a href="{{ item.image_3.url }}">
                                    <img src="{{ item.image_3.url }}" className="img-fluid" alt=""/>
                                </a>
                            </div>

                            <div className="col-lg-4 col-md-6 mb-4">
                                <a href="{{ item.image_4.url }}">
                                    <img src="{{ item.image_4.url }}" className="img-fluid" alt=""/>
                                </a>
                            </div>

                        </div>

                    </div>
                </main>
            }
        </div>
    )
}


const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
  })
export default connect(mapStateToProps, { removeFromCart, addToCart })(Detail);